import React, { Component } from 'react';
import { Container, Header, Segment, Grid, List } from 'semantic-ui-react';

class Contect extends Component {
  render() {
    return (
      <Segment inverted vertical style={{ padding: '3em 0em' }}>
        <Container>
          <span id='contect'></span>
          <Header
            inverted
            as='h2'
            content='Contect me' 
            textAlign='center'
            style={{
              marginBottom: '5vh',
              fontSize: '4rem'
            }}
          />
          <Grid divided inverted centered stackable>
            <Grid.Column width={8}>              
              <List link inverted style={{ fontSize: '2rem' }}>
                <List.Item>E-Mail : floor3th@gmail.com</List.Item>
                <List.Item>Phone : 010-9755-7989</List.Item>
                <List.Item>Github: https://github.com/SMKim94</List.Item>
              </List>
            </Grid.Column>
          </Grid>
        </Container>
      </Segment>
    );
  }
}

export default Contect;