import React, { Component } from 'react';
import backgroundImg from './img/programming.jpg';
// import './Home.css';

class Home extends Component {
  render() {
    return (
      <div 
        className='home'
        style={{
          position: 'relative',
          display: 'flex',
          width: '100%',
          height: '100vh',
          flexDirection: 'row',
          justifyContent: 'center',
          alignItems: 'center'
        }}
      >
        <div 
          className='background'
          style={{
            position: 'absolute',
            display: 'block',
            width: '100%',
            height: '100%',
            top: '0',
            left: '0',
            backgroundImage: `url(${backgroundImg})`,
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'center center',
            backgroundSize: 'cover',
            backgroundAttachment: 'fixed',
            opacity: '0.3',
            zIndex: '-1'
          }}
        ></div>
        <h1 
          className='home-title'
          style={{
            fontSize: '7rem',
            fontWeight: 'bold',
            textAlign: 'center'
          }}
        >김성민의 포트폴리오입니다.</h1>
      </div>
    );
  }
}

export default Home;