import React, { Component } from 'react';
import { Container, Header, Grid, Image } from 'semantic-ui-react';
import htmlLogo from './img/logo/HTML5_Logo.svg';
import cssLogo  from './img/logo/CSS3_logo_and_wordmark.svg.png';
import javascriptLogo from './img/logo/js.svg';
import javaLogo from './img/logo/java.svg';

class Skill extends Component {
  render() {
    return (
      <Container fluid
        id='skills'
        textAlign='center'
        style={{
          padding: '5vh 1vh',
          backgroundColor: 'lightgray'
        }}
      >
        <Header
          as='h2'
          content='Skills'
          style={{
            marginBottom: '5vh',
            fontSize: '4rem'
          }}
        />
        <Grid columns={2} relaxed='very'>
          <Grid.Row>
            <Grid.Column 
              width={4}
              style={{
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'center',
                alignItems: 'center'
              }}
            >
              <Header 
                as='h3' 
                content='Language'
                style={{
                  fontSize: '3rem'
                }}
              />
            </Grid.Column>     
            <Grid.Column 
              width={12} 
              style={{ 
                paddingLeft: '5vw',
                textAlign: 'left'
              }}
            >
              <Image.Group
                size='small' 
                bordered
              > 
                <Image src={javaLogo} />
                <Image src={javascriptLogo} />
              </Image.Group>
            </Grid.Column>
          </Grid.Row>
          <Grid.Row>
            <Grid.Column
                width={4}
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                  alignItems: 'center'
                }}
            >
              <Header 
                as='h3' 
                content='Web'
                style={{
                  fontSize: '3rem'
                }}
              />
            </Grid.Column>
            <Grid.Column 
              width={12} 
              style={{
                display: 'flex',
                flexDirection: 'row'
              }}
            >
              <Image src={htmlLogo} size='small' />
              <Image src={cssLogo} size='small' />
            </Grid.Column>
          </Grid.Row>
        </Grid>
        
      </Container>
    );
  }
}

export default Skill;