import React, { Component } from 'react';
import { Grid, Header } from 'semantic-ui-react';
import './About.css';

class About extends Component {
  render() {
    return (
      <div      
        style={{
          width: '100%'
        }}
      >
        <div id='about' style={{ marginBottom: '5vh' }}><br /></div>
        <Grid style={{ margin: '5vh 0' }}>     
          <Grid.Row>
            <Grid.Column width={6}>
              <Header 
                as='h2' 
                content='About me'
                style={{
                  textAlign: 'center',
                  fontSize: '4rem',
                  lineHeight: '30vh',
                  verticalAlign: 'middle'
                }}
              />
            </Grid.Column>
            <Grid.Column width={10}>
              <Header
                as='h3'
                content='내가 되고싶은 사람'
                style={{
                  paddingBottom: '3vh',
                  fontSize: '2.5rem'
                }}
              />
              <p 
                style={{
                  fontSize: '1.5rem'
                }}>
                나에 대해 소개하기
              </p>
            </Grid.Column>
          </Grid.Row>
        </Grid>     
      </div>
    );
  }
}

export default About;